export const GET_POKEMON_DETAILS = 'GET_POKEMON_DETAILS';
export const POST_POKEMON = 'POST_POKEMON';
export const GET_POKEMONS = 'GET_POKEMONS';

export function getPokemonDetails (id) {
    return function (dispatch) {
        fetch (`https://localhost:3001/pokemons/${id}`)
        .then ((response) => response.json())
        .then ((json) => {
            dispatch ({type: GET_POKEMON_DETAILS, payload: json});
        })}}

export function postPokemon (payload) {
    return {type: POST_POKEMON, payload: payload};
}

export function getPokemons() {
    return function (dispatch) {
        fetch (`https://localhost:3001/pokemons`)
        .then ((response) => response.json())
        .then ((json) => {
            dispatch({
            type: GET_POKEMONS, payload: json
            })
        })
    }
}