import React from 'react';
import {connect} from 'react-redux';
import { getPokemonDetails } from '../../actions';

class Pokemon extends React.Component {
    componentDidMount() {
        const pokemonID = this.props.match.params.id;
        this.props.getPokemonDetails(pokemonID)
    }
    render () {
        return (
            <div className="pokemon-detail">
                <h3>{this.props.pokemon.name}</h3>
                <p>{this.props.pokemon.types}</p>
                <p>{this.props.pokemon.id}</p>
                <p>{this.props.pokemon.hp}</p>
                <img src={this.props.pokemon.img} alt="Img not found" />


            </div>
        )
    }
}
function mapStateToProps(state) {
    return {
        pokemon: state.pokemonDetail
    }
}
function mapDispatchToProps(dispatch) {
    return {
        getPokemonDetails: id => dispatch(getPokemonDetails(id))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Pokemon)